import React, { Component, memo } from "react";
import queryString from 'query-string';
import { isMobile, isTablet } from 'react-device-detect';
import { Helmet } from "react-helmet";
import { CloudUrl } from "../../../constants";
import { getThemeImageFromWallID, getCurrentMilliseconds, getCurrentSeconds, getCurrentMinutes, getCurrentHours, getTextColor, rgbaToRgb } from '../../../utils'
import { hashtagCampaignImage } from '../../../actions/themeActions'
import { FOR_TEST_SERVER, suiteTrack, isTagEmbed, isTagShop, isTagShopEditor, isTagbox, isEmbed, isSocialWall } from "../../../actions/api";
import { StyleConstant } from "./StyleConstant";

const defaultURL = `https://cloud.taggbox.com/wall-branding/wallId_widget_`
const currentTime = getCurrentMilliseconds() + 1000 * (getCurrentSeconds() + 60 * (getCurrentMinutes() + 60 * getCurrentHours()));

const TwitterScriptRender = () => {
    return <script async>
        {`window.twttr = (function (d, s, id) {
                            var js, fjs = d.getElementsByTagName(s)[0],
                                t = window.twttr || {};
                            if (d.getElementById(id)) return t;
                            js = d.createElement(s);
                            js.id = id;
                            js.src = "https://platform.twitter.com/widgets.js";
                            fjs.parentNode.insertBefore(js, fjs);

                            t._e = [];
                            t.ready = function (f) {
                                                    t._e.push(f);
                            };

                            return t;
                            }(document, "script", "twitter-wjs"));`
        }
    </script>
}

const InHeritStyleRender = (wall) => {
    const { cardStyle, ctaBtn, button, popUpProperties, ProductSettings, UgcSettings, heading, subheading, ticker, height } = StyleConstant(wall)
    const styleId = `style-root-${wall.Wall.id}`;
    const isRendered = document.querySelectorAll('[data-wall-id]')?.length > 0 || document.querySelectorAll('[data-widget-id]')?.length > 0;

    const rootStyle = `${isRendered ? `[data-wall-id],[data-widget-id]` : `:root,[data-widget-id]`} {
                        --tb-color: ${cardStyle.color};
    --tb-font-size:${cardStyle.fontSize};
    --tb-font: ${cardStyle.font};
    --tb-font-weight: ${cardStyle.fontWeight};
    --tb-font-tyle: ${cardStyle.fontStyle};
    --tb-icon-color:${cardStyle.iconColor};
    --tb-line-height: ${cardStyle.lineHeight};
    --tb-link-color: ${cardStyle.linkColor};
    --tb-bg-color: ${cardStyle.background};
    --tb-background-size: contain;
    --tb-author-color: ${cardStyle.authorColor};
    --tb-author-font: ${cardStyle.authorFont};
    --tb-author-font-weight: ${cardStyle.authorFontWeight};
    --tb-author-font-style: ${cardStyle.authorFontStyle};
    --tb-border-radius: ${cardStyle.radius};
    --tb-icon-position:${cardStyle.iconPosition};
    --tb-media-size:${cardStyle.cardSize};
    --tb-padding:${cardStyle.cardPadding};
    --tb-grid-row:${cardStyle.rows};

    --tb-cta-color:${ctaBtn.color};
    --tb-cta-bg-color:${ctaBtn.background};
    --tb-cta-border-color:${ctaBtn.borderColor};
    --tb-cta-color-hover:${ctaBtn.colorHover};
    --tb-cta-bg-color-hover:${ctaBtn.backgroundHover};
    --tb-cta-border-color-hover:${ctaBtn.borderHover};
    --tb-cta-border-radius:${ctaBtn.radius};
    --tb-cta-padding:${ctaBtn.padding};
    --tb-cta-height:${ctaBtn.minHeight};
    --tb-cta-width:${ctaBtn.minWidth};
    --tb-cta-font:${ctaBtn.font};
    --tb-cta-font-weight:${ctaBtn.weight};
    --tb-cta-transition:${ctaBtn.transition};

    --tb-btn-color:${button.color};
    --tb-btn-bg-color:${button.background};
    --tb-btn-border-color:${button.borderColor};
    --tb-btn-color-hover:${button.colorHover};
    --tb-btn-bg-color-hover:${button.backgroundHover};
    --tb-btn-border-color-hover:${button.borderHover};
    --tb-btn-border-radius:${button.radius};
    --tb-btn-padding:${button.padding};
    --tb-btn-height:${button.minHeight};
    --tb-btn-width:${button.minWidth};
    --tb-btn-font:${button.font};
    --tb-btn-font-weight:${button.weight};
    --tb-btn-transition:${button.transition};

    --tb-popup-color:${popUpProperties.color};
    --tb-popup-bg-color:${popUpProperties.background};
    --tb-popup-font:${popUpProperties.font};
    --tb-popup-font-size:${popUpProperties.fontSize};
    --tb-popup-icon-color:${popUpProperties.iconColor};
    --tb-popup-font-weight:${popUpProperties.fontWeight};
    --tb-popup-font-style: ${popUpProperties.fontStyle};

    --tb-popup-author-font:${popUpProperties.authorFont};
    --tb-popup-author-font-size:${popUpProperties.authorFontSize};
    --tb-popup-author-font-weight:${popUpProperties.authorfontWeight};
    --tb-popup-author-font-style:${popUpProperties.authorfontWeight};
    --tb-popup-author-color:${popUpProperties.authorColor};


    --tb-catalogue-font:${ProductSettings.CatalogueTitle.font};
    --tb-catalogue-varient:${ProductSettings.CatalogueTitle.varient};
    --tb-catalogue-weight:${ProductSettings.CatalogueTitle.weight};
    --tb-catalogue-size:${ProductSettings.CatalogueTitle.size};
    --tb-catalogue-color:${ProductSettings.CatalogueTitle.color};

    --tb-product-font:${ProductSettings.ProductTitle.font};
    --tb-product-varient:${ProductSettings.ProductTitle.varient};
    --tb-product-size:${ProductSettings.ProductTitle.size};
    --tb-product-color:${ProductSettings.ProductTitle.color};

    --tb-price-font:${ProductSettings.Price.font};
    --tb-price-weight:${ProductSettings.Price.weight};
    --tb-price-varient:${ProductSettings.Price.varient};
    --tb-price-size:${ProductSettings.Price.size};
    --tb-price-color:${ProductSettings.Price.color};

    --tb-shop-btn-font:${ProductSettings.Button.font};
    --tb-shop-btn-weight:${ProductSettings.Button.weight};
    --tb-shop-btn-varient:${ProductSettings.Button.varient};
    --tb-shop-btn-size:${ProductSettings.Button.size};
    --tb-shop-btn-color:${ProductSettings.Button.color};
    --tb-shop-btn-bg-color:${ProductSettings.Button.background};

    --tb-hotspot-color:${ProductSettings.Hotspot.color};
    --tb-hotspot-bg-color:${ProductSettings.Hotspot.background};

    --tb-su-btn-font:${UgcSettings?.font};
    --tb-su-btn-varient:${UgcSettings?.varient};
    --tb-su-btn-weight:${UgcSettings?.weight};
    --tb-su-btn-size:${UgcSettings?.size};
    --tb-su-btn-color:${UgcSettings?.color};
    --tb-su-btn-background:${UgcSettings?.background};

    --tb-t-font-size:${heading.title.size};
    --tb-t-font-family:${heading.title.font};
    --tb-t-font-weight:${heading.title.fontWeight};
    --tb-t-color:${heading.title.color};
    --tb-t-text-align:${heading.title.textalign};

    --tb-st-font-size:${subheading.subtitle.size};
    --tb-st-font-family:${subheading.subtitle.font};
    --tb-st-font-weight:${subheading.subtitle.fontWeight};
    --tb-st-color:${subheading.subtitle.color};
    --tb-st-text-align:${subheading.subtitle.textalign};
    --tb-ti-title-font-size:${ticker.heading.size};
                        --tb-ti-title-font-family:${ticker.heading.font};
                        --tb-ti-title-font-weight:${ticker.heading.fontWeight};
                        --tb-ti-title-color:${ticker.heading.color};
                        --tb-ti-title-background:${ticker.heading.background};

                        --tb-ti-desc-font-size:${ticker.desc.size};
                        --tb-ti-desc-font-family:${ticker.desc.font};
                        --tb-ti-desc-font-weight:${ticker.desc.fontWeight};
                        --tb-ti-desc-color:${ticker.desc.color};
                        --tb-ti-desc-background:${ticker.desc.background};
                        --tb-theme-height:calc(100vh - ${height.banner} - ${height.ticker});
                        --tb-banner-height:${height.banner};
                        --tb-ticker-height:${height.ticker};
}`
    if (!document.getElementById(styleId)) {
        const style = document.createElement('style');
        style.type = 'text/css';
        style.id = styleId;
        style.innerHTML = rootStyle
        document.head.appendChild(style)
    }

}
const onLoadRemoveCss = (wallId) => {
    try { [`#style-root-${wallId}`, `#css-${wallId}`].forEach(selector => document.querySelector(selector)?.remove()); }
    catch (ex) { }
}
const customCssAdd = (wall) => {
    const styleId = `css-${wall.Wall.id}`;
    if (!document.getElementById(styleId) && wall.Personalization.cssStatus === 1) {
        const style = document.createElement('style');
        style.type = 'text/css';
        style.id = styleId;
        style.innerHTML = wall.Personalization.css
        document.head.appendChild(style)
    }
}
const addCustomLang = () => {
    try {
        const parsed = queryString.parse(window.location.search);
        var htmlElement = document.documentElement;
        if ((parsed && parsed.lang)) htmlElement.setAttribute('lang', parsed.lang);
        else htmlElement.setAttribute('lang', 'de');
    }
    catch (e) {
        console.error(e)
    }
}
class AppHead extends Component {
    constructor(props) {
        super(props)
        this.state = {
            hashtag_campaign_image: props.wall.Wall.id && window.location.href.includes("hashtag_campaign") ? `${defaultURL}${props.wall.Wall.id}.jpg?v=${currentTime}` : "",
        }
    }

    onLoadModule = (props) => {
        const isHastag = window.location.href.includes("hashtag_campaign") ? false : false
        const { wall } = props;
        const wallId = wall.Wall.id;
        const ownerId = wall.Wall.owner;

        if (isHastag) {
            // hashtagCampaignImage({
            //     wallId: wallId,
            //     domainName: FOR_TEST_SERVER ? suiteTrack ? `https://test.taggbox.com/embed-widget/lite/${wallId}?ugcSuite=1&hashtag_campaign=true` : `https://test.taggbox.com/embed-widget/lite/${wall.Wall.url}?hashtag_campaign=true` : suiteTrack ? `https://widget.taggbox.com/${wallId}?ugcSuite=1&hashtag_campaign=true` : `https://widget.taggbox.com/${wallId}`,
            //     ownerId: ownerId,
            //     screenshotSize: ["1200x624"]
            // }).then(({ data }) => {
            //     const { responseCode, responseData } = data;
            //     if (responseCode == 200 && responseData.length > 0 && responseData[0] && responseData[0].s3Url) {
            //         const currentTime = getCurrentMilliseconds() + 1000 * (getCurrentSeconds() + 60 * (getCurrentMinutes() + 60 * getCurrentHours()));
            //         this.setState({ hashtag_campaign_image: `${responseData[0].s3Url}?v=${currentTime}` })
            //     }
            //     else {
            //         const shareData = getThemeImageFromWallID(wall.Personalization.widgetTheme)
            //         this.setState({ hashtag_campaign_image: shareData })
            //     }
            // })
            //     .catch((error) => {
            const shareData = getThemeImageFromWallID(wall.Personalization.widgetTheme)
            this.setState({ hashtag_campaign_image: shareData })
            //})
        }
    }
    componentDidMount() {
        this.onLoadModule(this.props)
    }
    componentWillReceiveProps(newProps) {
        if (newProps != this.props && isTagShopEditor) {
            onLoadRemoveCss(newProps?.wall?.Wall?.id)
            this.onLoadModule(newProps)
        }
    }
    render() {
        const { hashtag_campaign_image } = this.state;
        const { wall } = this.props;

        const queryParams = new URLSearchParams(window.location.search);
        const previewId = queryParams.get('preview');
        const themeID = wall.Personalization.widgetTheme;
        const isHastag = window.location.href.includes("hashtag_campaign") ? false : false
        const brandName = suiteTrack ? `Tagbox UGC Suite` : isTagEmbed ? `Tagembed Widget` : isSocialWall ? ` Live SocialWall - Tagbox` : `Taggbox`

        var isNotFonts = ['inherit', 'Default']
        var allFonts = ['Inter'];

        if (wall.UgcSettings && wall.UgcSettings.onsite_link_font_btn && !isNotFonts.includes(wall.UgcSettings.onsite_link_font_btn)) allFonts.push(wall.UgcSettings.onsite_link_font_btn)

        if (wall.UgcSettings && wall.UgcSettings.onsite_link_font_msg && !isNotFonts.includes(wall.UgcSettings.onsite_link_font_msg)) allFonts.push(wall.UgcSettings.onsite_link_font_msg)

        if (wall.ThemeRule.link_font && !isNotFonts.includes(wall.ThemeRule.link_font)) allFonts.push(wall.ThemeRule.link_font)

        if (wall.ThemeRule.linkfontAuthor && !isNotFonts.includes(wall.ThemeRule.linkfontAuthor)) allFonts.push(wall.ThemeRule.linkfontAuthor)

        if (wall.Banner && Object.keys(wall.Banner).length) {
            if (wall.Banner.subtitle_link_font && !isNotFonts.includes(wall.Banner.subtitle_link_font)) allFonts.push(wall.Banner.subtitle_link_font)
            if (wall.Banner.title_link_font && !isNotFonts.includes(wall.Banner.title_link_font)) allFonts.push(wall.Banner.title_link_font)
        }

        if (wall.ProductSetting && Object.keys(wall.ProductSetting).length) {
            if (wall.ProductSetting.Button.fontLink && !isNotFonts.includes(wall.ProductSetting.Button.fontLink)) allFonts.push(wall.ProductSetting.Button.fontLink)
            if (wall.ProductSetting.CatalogueTitle.fontLink && !isNotFonts.includes(wall.ProductSetting.CatalogueTitle.fontLink)) allFonts.push(wall.ProductSetting.CatalogueTitle.fontLink)
            if (wall.ProductSetting.Price.fontLink && !isNotFonts.includes(wall.ProductSetting.Price.fontLink)) allFonts.push(wall.ProductSetting.Price.fontLink)
            if (wall.ProductSetting.ProductTitle.fontLink && !isNotFonts.includes(wall.ProductSetting.ProductTitle.fontLink)) allFonts.push(wall.ProductSetting.ProductTitle.fontLink)
        }

        if (wall.UgcSettings && Object.keys(wall.UgcSettings).length) {
            if (wall.UgcSettings.onsite_css_font_btn && !isNotFonts.includes(wall.UgcSettings.onsite_css_font_btn)) allFonts.push(wall.UgcSettings.onsite_css_font_btn)
            if (wall.UgcSettings.onsite_link_font_msg && !isNotFonts.includes(wall.UgcSettings.onsite_link_font_msg)) allFonts.push(wall.UgcSettings.onsite_link_font_msg)
        }

        if (wall.CustomTicker && Object.keys(wall.CustomTicker).length) {
            if (wall.CustomTicker.title_link_font && !isNotFonts.includes(wall.CustomTicker.title_link_font)) allFonts.push(wall.CustomTicker.title_link_font)
            if (wall.CustomTicker.subtitle_link_font && !isNotFonts.includes(wall.CustomTicker.subtitle_link_font)) allFonts.push(wall.CustomTicker.subtitle_link_font)
        }

        var modifiedFonts = allFonts.map(font => font.replace(/ /g, '+'));
        allFonts = [...new Set(modifiedFonts)];
        return <>{
            isHastag ? <Helmet>

                {suiteTrack ? <link rel="icon" href="https://ugc.taggbox.com/favicon.ico"  as="image"/> : null}

                <meta property="og:type" content="website" />
                <meta property="og:url" content={`https://widget-lite.taggbox.com/${wall.Wall.id}`} />
                <meta property="og:title" content={`${wall.Wall.name} - ${brandName} Widget`} />
                <meta property="og:description" content={`${wall.Wall.name} hashtag campaign created using ${brandName} Widget.`} />
                <meta property="og:image" content={hashtag_campaign_image} />


                <meta property="type" content="website" />
                <meta property="url" content={`https://widget-lite.taggbox.com/${wall.Wall.id}`} />
                <meta property="title" content={`${wall.Wall.name} - ${brandName} Widget`} />
                <meta property="description" content={`${wall.Wall.name} hashtag campaign created using ${brandName} Widget.`} />
                <meta property="image" content={hashtag_campaign_image} />

                {previewId ?
                    document.body.classList.add(`tb_previw_th-${themeID}`)
                    : null
                }

                {allFonts.map(function (font, index) {
                    return font != "0" ? <link key={index} href={`${CloudUrl}/web-fonts/${font}.css`} rel="stylesheet" crossorigin="anonymous"/> : null
                })}

                {isEmbed && TwitterScriptRender()}


                {
                    (isEmbed && wall.Wall && wall.Wall.google_tacking_status) ? <script async src="https://www.googletagmanager.com/gtag/js"></script> : null
                }


                {
                    (wall.Wall && wall.Wall.google_tacking_status) ? <script async>
                        {`window.dataLayer = window.dataLayer || [];
                    function gtag() {dataLayer.push(arguments); }
                    gtag("js", new Date());
                    window.gaTokenClient = '${wall.Wall.google_tacking_code}';
                    gtag("event", "page_view", {
                        "send_to": window.gaTokenClient,
                    "anonymize_ip": true,
                    });`}

                    </script> : null
                }
            </Helmet>
                :
                window.location.href.includes("widget.taggbox.com") || window.location.href.includes("widget-test.tagembed.com") || window.location.href.includes("widgets.tagembed.com") || window.location.href.includes("widget-test.tagshop.ai") || window.location.href.includes("widget.tagshop.ai") || window.location.href.includes("display-test.socialwalls.com") || window.location.href.includes("display.socialwalls.com") || window.location.href.includes("widget.socialwalls.com") || window.location.href.includes("display.taggbox.com") ? <>
                    <Helmet>
                        {suiteTrack ? <link rel="icon" href="https://ugc.taggbox.com/favicon.ico"  /> : isTagEmbed ? <link rel="icon" href="https://tagembed.com/wp-content/uploads/2020/09/favicon.ico" /> : isSocialWall ? <link rel="icon" href="https://cloud.tagbox.com/website/socialwalls/assets/img/socialwalls-32x32.png" /> : null}
                        <title>{`${wall.Wall.name} | ${brandName}`}</title>

                        <meta property="og:type" content="website" />
                        <meta property="og:url" content={isTagEmbed ? `https://widget.tagembed.com/${wall.Wall.id}` : `https://widget-lite.taggbox.com/${wall.Wall.id}`} />
                        <meta property="og:title" content={`${wall.Wall.name} - ${brandName} Widget`} />
                        <meta property="og:description" content={`${wall.Wall.name} featuring Social Feed using ${brandName} Widget. It helps brands to curate & embed social feeds, user reviews, UGC etc. on websites to boost sales, traffic, & engagement.`} />
                        <meta property="og:image" content={hashtag_campaign_image} />


                        <meta property="type" content="website" />
                        <meta property="url" content={isTagEmbed ? `https://widget.tagembed.com/${wall.Wall.id}` : `https://widget-lite.taggbox.com/${wall.Wall.id}`} />
                        <meta property="title" content={`${wall.Wall.name} - ${brandName} Widget`} />
                        <meta property="description" content={`${wall.Wall.name} featuring Social Feed using ${brandName} Widget. It helps brands to curate & embed social feeds, user reviews, UGC etc. on websites to boost sales, traffic, & engagement.`} />
                        <meta property="image" content={hashtag_campaign_image} />
                        {previewId ?
                            document.body.classList.add(`tb_previw_th-${themeID}`)
                            : null
                        }


                        {allFonts.map(function (font, index) {
                            return font != "0" ? <link key={index} href={`${CloudUrl}/web-fonts/${font}.css`} rel="stylesheet" crossorigin="anonymous" /> : null
                        })}





                        {isEmbed && TwitterScriptRender()}


                        {
                            (isEmbed && wall.Wall && wall.Wall.google_tacking_status) ? <script async src="https://www.googletagmanager.com/gtag/js"></script> : null
                        }
                        {
                            (wall.Wall && wall.Wall.google_tacking_status) ? <script async>
                                {`window.dataLayer = window.dataLayer || [];
                    function gtag() {dataLayer.push(arguments); }
                    gtag("js", new Date());
                    window.gaTokenClient = '${wall.Wall.google_tacking_code}';
                    gtag("event", "page_view", {
                        "send_to": window.gaTokenClient,
                    "anonymize_ip": true,
                    });`}

                            </script> : null
                        }
                    </Helmet>
                    {
                        wall.Wall.owner == 132857 && isTagbox ? addCustomLang() : null
                    }
                </>
                    :
                    <Helmet>


                        {/* {suiteTrack ? <link rel="icon" href="https://ugc.taggbox.com/favicon.ico" /> : null} */}

                        {previewId ?
                            document.body.classList.add(`tb_previw_th-${themeID}`)
                            : null
                        }

                        {allFonts.map(function (font, index) {
                            return font != "0" ? <link key={index} href={`${CloudUrl}/web-fonts/${font}.css`} rel="stylesheet" crossorigin="anonymous"/> : null
                        })}




                        {isEmbed && TwitterScriptRender()}


                        {
                            (isEmbed && wall.Wall && wall.Wall.google_tacking_status) ? <script async src="https://www.googletagmanager.com/gtag/js"></script> : null
                        }
                        {
                            (isEmbed && wall.Wall && wall.Wall.google_tacking_status) ? <script async>
                                {`window.dataLayer = window.dataLayer || [];
                    function gtag() {dataLayer.push(arguments); }
                    gtag("js", new Date());
                    window.gaTokenClient = '${wall.Wall.google_tacking_code}';
                    gtag("event", "page_view", {
                        "send_to": window.gaTokenClient,
                    "anonymize_ip": true,
                    });`}

                            </script> : null
                        }
                    </Helmet >}
            {InHeritStyleRender(wall)}
            {customCssAdd(wall)}
        </>
    }
}

export default memo(AppHead);